<template>
<Transition name="modal">
    <div class="modal" @click="closeModal"> 
    <div class="modal__block" @click.stop=""> 
        <h2 class="modal__title">{{edit ? $t('titleWindowedit') : $t('titleWindow')}}</h2>
        <div class="modal__inputs">
            <label>
                <span>Title</span>
                <input type="text" v-model="title"> 
            </label>
            <label>
                <span>Content</span>
                <textarea v-model="desc"></textarea>
            </label>
        </div>

        <div class="modal__btns">
            <button class="modal__btn cancel" 
            @click="closeModal">{{$t('closebtn')}}</button>
            <button v-if="!edit" class="modal__btn add" 
            @click="addNote">{{$t('addbtn')}}</button>
            <button v-else class="modal__btn add" 
            @click="changeNote">{{$t('editWindowbtn')}}</button>
        </div>
    </div>
  </div>
</Transition>
</template>

<script>

import { v4 as uuidv4 } from 'uuid';

export default {
    props: {
        currentId: Number,
        edit: Boolean,
        editNote: Object,
    },
    data(){
        return {
            id: this.currentId,
            title: '',
            desc: '',
        }
    },
    methods: {
        closeModal(){
            this.$emit('closeModal', false)
            this.title = '';
            this.desc = '';
        },
        addNote(){
            if(this.title != '' && this.desc !=''){
                const item = {
                id: uuidv4(),
                title: this.title,
                desc: this.desc,
                date: new Date().toLocaleDateString()
            }
            this.$emit('addNote', item)
            this.title = '';
            this.desc = '';
            }
        },
        changeNote(){
            if(this.title != '' && this.desc !=''){
                const editedNote = {
                    id: this.editNote.id,
                    title: this.title,
                    desc: this.desc,
                    date: new Date().toLocaleDateString()
                }
                this.$emit('editedNote', editedNote);
                this.closeModal()
            }
        }
    }
};
</script>

<style>

.modal {
    background: rgba(0, 0, 0, 0.35);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;
}

.modal__block {
    background: linear-gradient(0deg, rgba(103, 80, 164, 0.11), rgba(103, 80, 164, 0.11)), #FFFBFE;
    border-radius: 28px;
    max-width: 312px;
    width: 100%;
    padding: 24px;
}

.modal__title {
    font-family: 'Roboto';
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    color: #1C1B1F;
    margin-bottom: 15px;
}

.modal__inputs {
    display: flex;
    flex-direction: column;
    row-gap: 16px;
    margin-bottom: 24px;
}

.modal__inputs label {
    position: relative;
}

.modal__inputs span {
    position: absolute;
    top: 8px;
    left: 16px;
    font-family: 'Roboto';
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.4px;
    color: #6750A4;
}

.modal__inputs input, .modal__inputs textarea {
    background: #E7E0EC;
    border-radius: 4px 4px 0px 0px;
    border-bottom: 1px solid #1C1B1F;
    width: 100%;
    padding: 23px 0 9px 16px;
    font-family: 'Roboto';
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.5px;
    color: #49454F;
}

.modal__btns {
    display: flex;
    justify-content: flex-end;
    column-gap: 8px;

}

.modal__btn {
    font-family: 'Roboto';
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.1px;
    text-transform: uppercase;
    padding: 10px 12px;
    transition: 500ms;
}

.cancel {
    color: #CF1B1B;
}
.cancel:hover {
    background: #FFE1E1;
}

.add {
    color: #6750A4;
}
.add:hover {
    background: #E6DDFF;
}

.modal-enter-active, .modal-leave-active {
    transition: 300ms linear;
}

.modal-enter-from, .modal-leave-to {
    opacity: 0;
    transform: scale(1.5);
}

</style>