<template>
  <Navbar 
  @searchValue="search = $event" 
  @changeLang="changeLang"/>
  <Notes  
  :notes="filterNotes"
  @delNote="delNote"
  @changeNote="changeNote"
  /> 
  <Modal
  :edit="edit"
  :editNote="editNote"
  v-show="modalOpen" 
  @closeModal="closeModal" 
  :currentId="currentId" 
  @addNote="addNote"
  @editedNote="editedNote"
  />
  <AddButton @openModal="openModal"/>
</template>

<script> 

import Navbar from "@/components/Navbar.vue"
import Notes from "@/components/Notes.vue"
import Modal from "@/components/Modal.vue"
import AddButton from "@/components/AddButton.vue"

export default {
  
  components: {
    Navbar,
    Notes,
    Modal,
    AddButton,
  },
  computed: {
    filterNotes(){
      return this.search ? this.notes.filter(note => note.title.toLowerCase().includes(this.search.toLowerCase())) : this.notes;
    }
  },
  data(){
    return {
      edit: false,
      editNote: {},
      search: '',
      modalOpen: false,
      notes: [
        // {id: 1, title: 'Vue', desc: 'Lorem ipsum dolor sit', date: '07.03.2022'},
        // {id: 2, title: 'React', desc: 'Lorem ipsum dolor sit', date: '07.03.2022'},
        // {id: 3, title: 'Angular', desc: 'Lorem ipsum dolor sit', date: '07.03.2022'},
      ]
    };
  },
  created(){
    this.getNotes();
  },
  methods: {
    openModal(){
      this.modalOpen = true
    },
    closeModal(status){
      this.modalOpen = status;
      setTimeout(()=> {
        this.edit= false;
      }, 500)
    },
    addNote(item){
      this.notes.push(item);
      this.modalOpen = false;
    },
    delNote(id){
      let index = this.notes.findIndex((note)=> note.id == id );
      this.notes.splice(index, 1);
    },
    getNotes(){
      let localNotes = localStorage.notes; 
      if(localNotes){
        this.notes = JSON.parse(localNotes);
        this.currentId = this.notes.length;
        this.currentId++;
      }
    },
    changeNote(id){
      this.modalOpen = this.edit = true;
      let pickedNote = this.notes.find(note => note.id == id);
      this.editNote = pickedNote;
    },
    editedNote(noteEdited){
      this.notes.forEach(note => {
        if (note.id == noteEdited.id) {
          note.title = noteEdited.title;
          note.desc = noteEdited.desc;
          note.date = noteEdited.date;
        }
      })
    },
    changeLang(val){
      this.lang = localStorage.lang = val;
    }
  },

  watch: {
    notes: {
      handler(newNotes){
        localStorage.notes = JSON.stringify(this.notes);
      },
      deep: true, 
    }
  }

}
</script>

